header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/* +++++++++++  CTA  +++++++++++ */
.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* +++++++++++  HEADER SOCIALS  +++++++++++ */
.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* +++++++++++  ME  +++++++++++ */

.me{
    /* background: linear-gradient(var(--color-primary),transparent); */
    width: 20rem;
    height: 22rem;
    position:absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    padding-bottom: 2rem;
    border: solid 4px var(--color-primary);
    overflow: hidden;
    border-radius: 2rem;
    
}
.me:hover{
    border-color: var(--color-white);
}
.me img{
   height: 480px;
   width: 320px;
}

/* +++++++++++  SCROLL DOWN  +++++++++++ */
.scroll__down{
   position: absolute;
   right: -2.3rem;
   bottom: 5rem;
   transform:rotate(90deg);
   font-weight: 300;
   font-size: 0.9rem;
}

/* =======MEDIA QUERIES (MEDIUM DEVICES)======== */

@media screen and (max-width:1024px) {
    header{
        height: 68vh;

    }
    .me img{
        height: 400px;
    }
    .me{
        height: 19.5rem;
    }
}


/* =======MEDIA QUERIES (SMALL DEVICES)======== */

@media screen and (max-width:600px) {
    header{
        height: 100vh;
        
    }

    .scroll__down{
        display: none;
    }
    
    .header__socials{
        display: none;
    }
    .me img{
        height: 300px;
        width: 235px;
    }
    .me{
        height: 17rem ;
        width: 15rem;
        left: calc(50% - 7.5rem);
    }
}
